<template>
  <div>
    <loading :active.sync="isLoading"></loading>
    <div class="table-responsive" v-if="orders.length > 0">
      <table class="table table-striped mt-5">
        <thead>
          <tr>
            <th width="140">è³¼è²·æé</th>
            <th>è³¼è²·é ç®</th>
            <th width="140">ä»æ¬¾éé¡</th>
            <th width="140">ä»æ¬¾æ¹å¼</th>
            <th width="140">æ¯å¦ä»æ¬¾</th>
            <th width="100">å®¢æ¶è³æ</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="item in sortOrder" :key="item.id">
            <td>{{ item.created.timestamp | date}}</td>
            <td>
              <ul class="list-unstyled">
                <li v-for="(product, i) in item.products" :key="i">
                  {{ product.product.title }} æ¸éï¼{{ product.quantity}} {{ product.product.unit }}
                </li>
              </ul>
            </td>
            <td class="text-center">{{ item.amount | money}}</td>
            <td class="text-center">{{ item.payment}}</td>
            <td>
              <div class="custom-control custom-switch">
                <input :id="item.id" v-model="item.paid" type="checkbox" class="custom-control-input" @change="updatePayment(item)">
                <label class="custom-control-label" :for="item.id">
                  <strong v-if="item.paid" class="text-success">å·²ä»æ¬¾</strong>
                  <span v-else class="text-muted">å°æªä»æ¬¾</span>
                </label>
              </div>
            </td>
            <td>
              <button class="btn btn-outline-primary btn-sm mr-1" @click.prevent="openModal(item)">
                æ¥ç
              </button>
            </td>
          </tr>
        </tbody>
      </table>
      <!-- åé  -->
      <Pagination :pages="pagination" @emit-pages="getOrders"></Pagination>

      <!-- Order Modal -->
      <OrderModal ref="orderModal"/>
    </div>
  </div>
</template>

<script>
import Toast from '@/utils/toast'
import Pagination from '@/components/Pagination'
import OrderModal from '@/components/backed/OrderModal'

export default {
  data () {
    return {
      orders: [],
      isLoading: true,
      pagination: {},
      order: {
        user: {}
      },
      isProcessing: false
    }
  },
  components: {
    Pagination,
    OrderModal
  },
  created () {
    this.getOrders()
  },
  methods: {
    getOrders (page = 1) {
      const api = `${process.env.VUE_APP_APIPATH}${process.env.VUE_APP_UUID}/admin/ec/orders?page=${page}`
      this.isLoading = true
      this.$http.get(api)
        .then(res => {
          this.isLoading = false
          this.orders = res.data.data
          this.pagination = res.data.meta.pagination
        })
        .catch(() => {
          this.isLoading = false
          Toast.fire({
            title: 'ç¡æ³åå¾è³æ',
            icon: 'error'
          })
        })
    },
    updatePayment (item) {
      let api = `${process.env.VUE_APP_APIPATH}${process.env.VUE_APP_UUID}/admin/ec/orders/${item.id}/paid`
      this.isLoading = true
      if (!item.paid) {
        api = `${process.env.VUE_APP_APIPATH}${process.env.VUE_APP_UUID}/admin/ec/orders/${item.id}/unpaid`
      }
      this.$http.patch(api, item.id)
        .then(() => {
          this.isLoading = false
          Toast.fire({
            title: 'ä¿®æ¹æå',
            icon: 'success'
          })
          this.getOrders()
        })
        .catch(() => {
          this.isLoading = false
          Toast.fire({
            title: 'ä¿®æ¹å¤±æ',
            icon: 'error'
          })
        })
    },
    openModal (item) {
      this.$refs.orderModal.getOrder(item.id)
    }
  },
  computed: {
    sortOrder () {
      const vm = this
      let newOrder = []
      if (vm.orders.length) {
        newOrder = vm.orders.sort((a, b) => {
          const aIsPaid = a.is_paid ? 1 : 0
          const bIsPaid = b.is_paid ? 1 : 0
          return bIsPaid - aIsPaid
        })
      }
      return newOrder
    }
  }
}
</script>
